:root {

	/* Z-index levels */
}

.root {
	padding-bottom: calc(13px / 2)
}

.hasOutput {
	padding-bottom: calc(calc(13px / 2) + 13px);
}

.inputWrapper {
	height: 13px;
	background-color: #fff;
	border: 1px solid #34495e;
}

.focused { }

.disabled .inputWrapper,
.readonly .inputWrapper {
	background-color: #bbb;
}

/* invalid style */

.invalid .inputWrapper {
	border-color: red;
}

.invalid .thumb::before {
	background-color: red;
}

/* valid style */

.valid .inputWrapper::before {
	border-color: green;
}

.valid .inputWrapper::after {
	background-color: green;
}

.focused input {
	outline: none;
}

.focused .thumb.focused {
	-webkit-box-shadow: 0 0 7px 3px #34495e;
	        box-shadow: 0 0 7px 3px #34495e;
	border-color: white;
}

/* required style, only applies to inputs with a label */

.required span::after {
	content: '*';
	color: red;
}

.thumb {
	background-color: #ffffff;
	border: 1px solid #34495e;
	width: calc(13px * 2);
	height: calc(13px * 2);
	-webkit-transform: translate(-50%, -25%);
	        transform: translate(-50%, -25%);
}

.filled {
	background-color: #34495e;
}

.input::-ms-thumb {
	height: 2em;
	width: 2em;
}

.input::-moz-range-thumb {
	height: 2em;
	width: 2em;
}

.input::-webkit-slider-thumb {
	height: 2em;
	width: 2em;
}

.outputTooltip {
	left: 2.5em;
	position: absolute;
	top: 0;
}

.output {
	display: block;
	margin-top: 16px;
}

